import React, { Component } from 'react';
import './App.css';
import './bootstrap.min.css';
import { Table } from 'react-bootstrap';
import $ from 'jquery';

class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {contents: []};
  }

  componentDidMount() {
    $.getJSON('http://whateverorigin.org/get?url=' + this.props.source + '&callback=?', function(data){
      console.log(data);
      this.setState(data);
    }.bind(this));
  }

  render() {
    var countries = this.state.contents.map(function(result) {
      result.points = (result.gold_count * this.props.goldValue) + (result.silver_count * this.props.silverValue) + (result.bronze_count * this.props.bronzeValue);
      return result;
    }.bind(this));

    countries.sort(function(a, b) {
      if (a.points !== b.points) {
        return b.points - a.points;
      }
      return b.gold_count - a.gold_count;
    });

    if (countries.length === 0) {
      return (
        <div>Loading...</div>
      );
    }

    return (
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>Official Rank</th>
            <th>Country</th>
            <th>Gold Medals</th>
            <th>Silver Medals</th>
            <th>Bronze Medals</th>
            <th>Points</th>
          </tr>
        </thead>
        <tbody>
          {
            countries.map(function(result) {
              return <tr key={result.id}>
                <td>{result.place}</td>
                <td>{result.country_name}</td>
                <td>{result.gold_count}</td>
                <td>{result.silver_count}</td>
                <td>{result.bronze_count}</td>
                <td>{result.points}</td>
              </tr>;
            })
          }
        </tbody>
      </Table>
    );
  }
}

export default Content;



