import React, { Component } from 'react';
import './App.css';
import './bootstrap.min.css';
import Content from './Content';
import ValuesBar from './ValuesBar';
import { Navbar, Grid } from 'react-bootstrap';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {goldValue: 9, silverValue: 3, bronzeValue: 1};
    this.handleUserInput = this.handleUserInput.bind(this);
  }

  handleUserInput(gold, silver, bronze) {
    this.setState({goldValue: gold, silverValue: silver, bronzeValue: bronze});
  }

  render() {
    return (
      <div>
      <Navbar>
        <Navbar.Header>
          <Navbar.Brand>
            Rio Medal Count
          </Navbar.Brand>
          <Navbar.Toggle />
        </Navbar.Header>
      </Navbar>
      <Grid>
        <ValuesBar onUserInput={this.handleUserInput} goldValue={this.state.goldValue} silverValue={this.state.silverValue} bronzeValue={this.state.bronzeValue}/>
        <br/>
        <Content source="http://www.medalbot.com/api/v1/medals" goldValue={this.state.goldValue} silverValue={this.state.silverValue} bronzeValue={this.state.bronzeValue}/>
      </Grid>
      </div>
    );
  }
}

export default App;



